import request from './utils/Request'

/**
 * 
 * @param {Object} token token
 * @param {Object} res
 * @param {Object} err
 */
export function findProjectListAPI(token, res, err) {
	request.get('/private/project', {
		headers: {
			'x-session': token
		}
	}).then(res).catch(err);
}

/**
 * 
 * @param {String} pid id
 * @param {String} token token
 * @param {Object} resLL
 * @param {Object} err
 */
export function getProjectAPI(pid, token, res, err) {
	request.get('/private/json/' + pid, {
		headers: {
			'x-session': token
		}
	}).then(res).catch(err);
}

/**
 * 
 * @param {String} pid id
 * @param {String} pwd 
 * @param {Object} resLL
 * @param {Object} err
 */
export function getProjectShareAPI(sid, pwd, res, err) {
	request.get('/project/share', {
		params: {
			sid: sid,
			pwd: pwd
		}
	}).then(res).catch(err);
}
